.card {
  width: 112px;
  height: 173px;
  background-position: center;
  background-repeat: no-repeat;
}

.card:not(:first-child) {
  margin-top: -141px;
}

[draggable='true'] {
  /*
   To prevent user selecting inside the drag source
  */
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

