@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
.header-bar {
  width: 100%;
  height: 54px;
  background-color: #000;
  box-shadow: 0 2px 0 #ffffff14;
  padding: 0 15px;
  display: flex;
}

.logo {
  width: 30%;
  height: 100%;
  padding-left: 6.5%;
}

.logo > span {
  font: italic normal bold 32px/42px PT Sans;
  letter-spacing: 0px;
  text-transform: uppercase;
  line-height: 54px;
}

.logo > span:first-child {
  color: #00ffce;
}

.logo > span:last-child {
  color: #a617ff;
}

.timer {
  width: 40%;
  text-align: center;
  height: 100%;
  display: inline-block;

  font: normal normal bold 32px/42px PT Sans;
  color: #fff;
  line-height: 54px;
}

.game-handle {
  width: 30%;
  height: 100%;
  padding-right: 6.5%;
}

.game-handle-list {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  display: flex;
}

.game-handle-list-item {
  margin: 0 25px;
  list-style: none;
  height: 100%;
  line-height: 54px;

  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  text-transform: uppercase;

  cursor: pointer;
  opacity: 0.75;
  transition: 0.3s;
}

.game-handle-list-item:hover {
  opacity: 1;
}

li[class*='handle-']::before {
  content: '';
  width: 24px;
  height: 100%;
  position: absolute;
  top: 0;
  left: -30px;
}

li[class*='handle-'] {
  position: relative;
}

.handle-undo::before {
  background: transparent url(/f2e2nd_week2/static/media/ic_undo_24px.d4d24bf5.svg) 50% 50%
    no-repeat padding-box;
}

.handle-pause::before {
  background: transparent url(/f2e2nd_week2/static/media/ic_pause_24px.b7a3f31a.svg) 50% 50%
    no-repeat padding-box;
}

.handle-restart::before {
  background: transparent url(/f2e2nd_week2/static/media/ic_loop_24px.da66fe01.svg) 50% 50%
    no-repeat padding-box;
}

.card {
  width: 112px;
  height: 173px;
  background-position: center;
  background-repeat: no-repeat;
}

.card:not(:first-child) {
  margin-top: -141px;
}

[draggable='true'] {
  /*
   To prevent user selecting inside the drag source
  */
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

