.main {
  display: flex;
  margin-top: 56px;
  justify-content: center;
  overflow: hidden;
}

.stacking-area {
  display: flex;
  margin-top: 48px;
}

.open-cells-wrapper,
.foundation-wrapper {
  display: flex;
  margin: 0 15px;
  width: 50%;
}

.open-cells-wrapper {
  justify-content: flex-end;
}

.cell-wrapper > .card-group > .card:not(:first-child) {
  margin-top: -173px !important;
}

[class*='foundation-box-'] {
  position: relative;
}

[class*='foundation-box-']::before {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  transform: translate(-50%, -50%);
  opacity: 0.1;
}

.foundation-box-spade::before {
  background: transparent
    url(/f2e2nd_week2/static/media/spade-16px-white.8803842b.svg) 50% 50% no-repeat
    padding-box;
}

.foundation-box-heart::before {
  background: transparent
    url(/f2e2nd_week2/static/media/heart-16px-white.9bf2862e.svg) 50% 50% no-repeat
    padding-box;
}

.foundation-box-diamond::before {
  background: transparent
    url(/f2e2nd_week2/static/media/diamond-16px-white.fb67652b.svg) 50% 50% no-repeat
    padding-box;
}

.foundation-box-club::before {
  background: transparent url(/f2e2nd_week2/static/media/club-16px-white.198e3642.svg)
    50% 50% no-repeat padding-box;
}

.cell-wrapper {
  min-width: 112px;
  height: 173px;
  display: flex;
  background-color: #ffffff14;
  border-radius: 4px;
}

.cell-wrapper:not(:first-child) {
  margin-left: 30px;
}

.card-group {
  display: flex;
  width: 112px;
  flex-direction: column;
}

.card-group:not(:first-child) {
  margin-left: 30px;
}

* {
  box-sizing: border-box;
  font-family: PT Sans;
}

html,
body {
  margin: 0 auto;
  padding: 0;
}

.App {
  width: 100%;
  min-height: 100vh;
  height: 100vh;
  background-color: #121212;
  overflow: hidden;
}

